.tooltip {
	position: absolute;
	width: 200px;
	height: 80px;

	/*padding-left: 15px;*/

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;

	z-index: 10;
	visibility: hidden;
	background-color: black;
	color: #2AB858;

	border: 1px solid grey;
	white-space: pre-line;
}


